import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { motion } from 'framer-motion';
import storageService from '../services/storageService';
import '../styles/BottomNavbar.css';

const BottomNavbar: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [currentPlan, setCurrentPlan] = useState<string>(storageService.getCurrentPlan());
  
  // Оновлюємо поточний план при монтуванні компонента
  useEffect(() => {
    setCurrentPlan(storageService.getCurrentPlan());
  }, []);
  
  const isActive = (path: string) => {
    return location.pathname === path;
  };

  return (
    <motion.nav 
      className="bottom-navbar"
      initial={{ y: 100 }}
      animate={{ y: 0 }}
      transition={{ duration: 0.3, type: 'spring', stiffness: 120 }}
    >
      <div 
        className={`nav-item ${isActive('/app') ? 'active' : ''}`}
        onClick={() => navigate('/app')}
      >
        <div className="icon">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
            <path d="M3 9l9-7 9 7v11a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2z"></path>
            <polyline points="9 22 9 12 15 12 15 22"></polyline>
          </svg>
        </div>
        <span>Завдання</span>
      </div>
      
      <div 
        className={`nav-item ${isActive('/analytics') ? 'active' : ''}`}
        onClick={() => navigate('/analytics')}
      >
        <div className="icon">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
            <line x1="18" y1="20" x2="18" y2="10"></line>
            <line x1="12" y1="20" x2="12" y2="4"></line>
            <line x1="6" y1="20" x2="6" y2="14"></line>
          </svg>
        </div>
        <span>Аналітика</span>
      </div>
      
      {currentPlan === 'free' && (
        <div 
          className="nav-item premium"
          onClick={() => navigate('/subscription')}
        >
          <div className="icon premium-icon">
            <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon>
            </svg>
          </div>
          <span>Преміум</span>
        </div>
      )}
      
      <div 
        className={`nav-item ${isActive('/profile') ? 'active' : ''}`}
        onClick={() => navigate('/profile')}
      >
        <div className="icon">
          <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
            <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
            <circle cx="12" cy="7" r="4"></circle>
          </svg>
        </div>
        <span>Профіль</span>
      </div>
    </motion.nav>
  );
};

export default BottomNavbar; 