import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { CheckIcon, XIcon, ChevronLeftIcon, AwardIcon } from './icons';
import '../styles/SubscriptionPage.css';

interface SubscriptionFeature {
  id: number;
  name: string;
  included: boolean;
}

interface SubscriptionPlan {
  id: number;
  name: string;
  description: string;
  monthlyPrice: number;
  yearlyPrice: number;
  features: SubscriptionFeature[];
  isPopular?: boolean;
}

const SubscriptionPage: React.FC = () => {
  const [isYearly, setIsYearly] = useState(false);
  
  const getYearlyDiscount = (monthlyPrice: number): number => {
    return parseFloat((monthlyPrice * 10).toFixed(2)); // 2 місяці безкоштовно
  };

  const subscriptionPlans: SubscriptionPlan[] = [
    {
      id: 1,
      name: 'Basic',
      description: 'Perfect for individuals and small projects',
      monthlyPrice: 9.99,
      yearlyPrice: 7.99,
      features: [
        { id: 1, name: 'Up to 5 projects', included: true },
        { id: 2, name: '5GB storage', included: true },
        { id: 3, name: '3 team members', included: true },
        { id: 4, name: 'Basic analytics', included: true },
        { id: 5, name: 'Priority support', included: false },
        { id: 6, name: 'Advanced security', included: false },
      ]
    },
    {
      id: 2,
      name: 'Professional',
      description: 'Ideal for growing teams and businesses',
      monthlyPrice: 24.99,
      yearlyPrice: 19.99,
      features: [
        { id: 1, name: 'Unlimited projects', included: true },
        { id: 2, name: '20GB storage', included: true },
        { id: 3, name: '10 team members', included: true },
        { id: 4, name: 'Advanced analytics', included: true },
        { id: 5, name: 'Priority support', included: true },
        { id: 6, name: 'Advanced security', included: false },
      ],
      isPopular: true
    },
    {
      id: 3,
      name: 'Enterprise',
      description: 'For large organizations with advanced needs',
      monthlyPrice: 49.99,
      yearlyPrice: 39.99,
      features: [
        { id: 1, name: 'Unlimited projects', included: true },
        { id: 2, name: 'Unlimited storage', included: true },
        { id: 3, name: 'Unlimited team members', included: true },
        { id: 4, name: 'Custom analytics', included: true },
        { id: 5, name: 'Dedicated support', included: true },
        { id: 6, name: 'Enterprise security', included: true },
      ]
    }
  ];

  // Animation variants
  const headerVariants = {
    hidden: { opacity: 0, y: -50 },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { 
        duration: 0.6, 
        ease: "easeOut" 
      }
    }
  };

  const cardVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: (custom: number) => ({
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.5,
        delay: custom * 0.1,
        ease: "easeOut"
      }
    }),
    hover: {
      y: -8,
      boxShadow: "0 15px 35px rgba(108, 92, 231, 0.15)",
      transition: {
        duration: 0.3
      }
    }
  };

  const buttonVariants = {
    hover: {
      scale: 1.03,
      boxShadow: "0 8px 20px rgba(108, 92, 231, 0.25)",
      transition: { duration: 0.2 }
    },
    tap: {
      scale: 0.97,
      boxShadow: "0 4px 10px rgba(108, 92, 231, 0.2)",
      transition: { duration: 0.1 }
    }
  };

  const popularButtonVariants = {
    hover: {
      scale: 1.03,
      boxShadow: "0 8px 20px rgba(255, 127, 80, 0.25)",
      transition: { duration: 0.2 }
    },
    tap: {
      scale: 0.97,
      boxShadow: "0 4px 10px rgba(255, 127, 80, 0.2)",
      transition: { duration: 0.1 }
    }
  };

  const featureItemVariants = {
    hidden: { opacity: 0, x: -10 },
    visible: (custom: number) => ({
      opacity: 1,
      x: 0,
      transition: {
        duration: 0.3,
        delay: custom * 0.05,
        ease: "easeOut"
      }
    }),
    hover: {
      x: 5,
      transition: { duration: 0.2 }
    }
  };

  const toggleBillingPeriod = () => {
    setIsYearly(!isYearly);
  };

  return (
    <div className="subscription-page">
      <motion.div 
        className="subscription-header"
        initial="hidden"
        animate="visible"
        variants={headerVariants}
      >
        <h1>Choose Your Plan</h1>
        <p>Select the perfect plan to fit your needs and budget</p>
        
        <div className="billing-toggle">
          <motion.span 
            className={!isYearly ? 'active' : ''}
            animate={{ opacity: !isYearly ? 1 : 0.7 }}
          >
            Monthly
          </motion.span>
          
          <label className="switch">
            <input type="checkbox" checked={isYearly} onChange={toggleBillingPeriod} />
            <span className="slider round"></span>
          </label>
          
          <motion.span 
            className={isYearly ? 'active' : ''}
            animate={{ opacity: isYearly ? 1 : 0.7 }}
          >
            Yearly
            <motion.span 
              className="discount-badge"
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ 
                scale: isYearly ? [0.9, 1.1, 1] : 0.9, 
                opacity: isYearly ? 1 : 0 
              }}
              transition={{ duration: 0.3 }}
            >
              Save 20%
            </motion.span>
          </motion.span>
        </div>
      </motion.div>

      <div className="plans-container">
        {subscriptionPlans.map((plan, index) => (
          <motion.div 
            key={plan.id}
            className={`plan-card ${plan.isPopular ? 'popular' : ''}`}
            custom={index}
            initial="hidden"
            animate="visible"
            whileHover="hover"
            variants={cardVariants}
          >
            {plan.isPopular && (
              <motion.div 
                className="popular-tag"
                initial={{ scale: 0, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ delay: 0.3, type: "spring", stiffness: 500 }}
              >
                <AwardIcon style={{ marginRight: '4px' }} /> Most Popular
              </motion.div>
            )}
            
            <h2 className="plan-name">{plan.name}</h2>
            <p className="plan-description">{plan.description}</p>
            
            <motion.div 
              className="plan-price"
              animate={{ 
                scale: [1, 1.05, 1],
                transition: { duration: 0.3, delay: index * 0.1 + 0.3 } 
              }}
            >
              <span className="currency">$</span>
              <span className="amount">
                {isYearly ? plan.yearlyPrice : plan.monthlyPrice}
              </span>
              <span className="period">/{isYearly ? 'year' : 'month'}</span>
            </motion.div>
            
            <ul className="features-list">
              {plan.features.map((feature, featureIndex) => (
                <motion.li 
                  key={feature.id} 
                  className={feature.included ? 'included' : 'excluded'}
                  custom={featureIndex}
                  initial="hidden"
                  animate="visible"
                  whileHover="hover"
                  variants={featureItemVariants}
                >
                  <div className="feature-icon">
                    {feature.included 
                      ? <CheckIcon /> 
                      : <XIcon />
                    }
                  </div>
                  <span className="feature-name">{feature.name}</span>
                </motion.li>
              ))}
            </ul>
            
            <motion.button 
              className="subscribe-button"
              variants={plan.isPopular ? popularButtonVariants : buttonVariants}
              whileHover="hover"
              whileTap="tap"
            >
              Subscribe Now
            </motion.button>
          </motion.div>
        ))}
      </div>

      <motion.div 
        className="subscription-footer"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.8 }}
      >
        <p>Have questions about our plans? <a href="#contact">Contact our sales team</a></p>
        <p>All plans come with a 14-day money-back guarantee</p>
        <a href="#" className="back-link">
          <ChevronLeftIcon style={{ verticalAlign: 'middle', marginRight: '5px' }} />
          Return to dashboard
        </a>
      </motion.div>
    </div>
  );
};

export default SubscriptionPage; 