import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import storageService from '../services/storageService';

// Define window with Telegram WebApp
declare global {
  interface Window {
    Telegram: {
      WebApp: {
        ready: () => void;
        expand: () => void;
        initData: string;
        initDataUnsafe: {
          user?: {
            id: number;
            first_name: string;
            last_name?: string;
            username?: string;
          };
        };
        onEvent: (eventType: string, eventHandler: (...args: any[]) => void) => void;
        offEvent: (eventType: string, eventHandler: (...args: any[]) => void) => void;
        sendData: (data: string) => void;
        MainButton: {
          text: string;
          color: string;
          textColor: string;
          isVisible: boolean;
          isActive: boolean;
          isProgressVisible: boolean;
          show: () => void;
          hide: () => void;
          enable: () => void;
          disable: () => void;
          showProgress: (leaveActive: boolean) => void;
          hideProgress: () => void;
          onClick: (callback: () => void) => void;
          offClick: (callback: () => void) => void;
        };
      };
    };
  }
}

const TelegramWebApp: React.FC = () => {
  const [isWebApp, setIsWebApp] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    // Check if we're running inside Telegram WebApp
    if (window.Telegram && window.Telegram.WebApp) {
      const tg = window.Telegram.WebApp;
      setIsWebApp(true);
      
      // Tell Telegram WebApp we're ready
      tg.ready();
      
      // Expand to full height
      tg.expand();
      
      // If user data is available, use it
      if (tg.initDataUnsafe?.user) {
        const { first_name, last_name, username } = tg.initDataUnsafe.user;
        const fullName = [first_name, last_name].filter(Boolean).join(' ');
        
        // Store Telegram user info in localStorage
        const telegramInfo = {
          username: username || '',
          name: fullName,
          connected: true
        };
        
        localStorage.setItem('telegram_user_info', JSON.stringify(telegramInfo));
        
        // If first time, redirect to app
        if (!storageService.getUserProfile().name) {
          // Create a new user profile with Telegram user's name
          const newProfile = {
            ...storageService.getUserProfile(),
            name: fullName,
            telegram: telegramInfo
          };
          storageService.saveUserProfile(newProfile);
          navigate('/app');
        }
      }
    }
  }, [navigate]);

  // If not a WebApp, return null (component won't affect the UI)
  if (!isWebApp) {
    return null;
  }

  return null; // This component doesn't render anything visible
};

export default TelegramWebApp; 