import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import storageService, { Task } from '../services/storageService';
import MotivationalQuote from './MotivationalQuote';
import TaskItem from './TaskItem';
import PremiumFeature from './PremiumFeature';

const TaskApp: React.FC = () => {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [newTask, setNewTask] = useState<Omit<Task, 'id' | 'createdAt'>>({
    title: '',
    description: '',
    status: 'pending',
    priority: 'medium',
    category: 'personal'
  });
  const [filter, setFilter] = useState({
    status: 'all',
    priority: 'all',
    category: 'all'
  });
  const [darkMode, setDarkMode] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [showAddForm, setShowAddForm] = useState(false);
  const [showMotivationalQuote, setShowMotivationalQuote] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState('personal');

  // Load tasks and theme preference from localStorage on startup
  useEffect(() => {
    const savedTasks = storageService.getTasks();
    setTasks(savedTasks);
    
    const themePreference = storageService.getThemePreference();
    setDarkMode(themePreference === 'dark');
    if (themePreference === 'dark') {
      document.body.classList.add('dark-mode');
    }
  }, []);

  // Save tasks to localStorage whenever they change
  useEffect(() => {
    storageService.saveTasks(tasks);
  }, [tasks]);

  // Handle form input changes
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setNewTask(prev => ({ ...prev, [name]: value }));
    
    // Показуємо мотиваційну цитату при зміні категорії
    if (name === 'category') {
      setSelectedCategory(value);
      setShowMotivationalQuote(true);
      // Автоматично ховаємо цитату через деякий час
      setTimeout(() => {
        setShowMotivationalQuote(false);
      }, 10000); // 10 секунд
    }
  };

  // Add new task
  const addTask = (e: React.FormEvent) => {
    e.preventDefault();
    const task = storageService.createTask(newTask);
    setTasks(prev => [...prev, task]);
    setNewTask({
      title: '',
      description: '',
      status: 'pending',
      priority: 'medium',
      category: 'personal'
    });
    setShowAddForm(false); // Hide form after adding
  };

  // Update task status
  const updateTaskStatus = (id: string, status: Task['status']) => {
    let updatedTasks;
    if (status === 'completed') {
      updatedTasks = storageService.completeTask(tasks, id);
    } else {
      updatedTasks = tasks.map(task => 
        task.id === id ? { ...task, status } : task
      );
    }
    setTasks(updatedTasks);
  };

  // Delete task
  const deleteTask = (id: string) => {
    setTasks(prev => prev.filter(task => task.id !== id));
  };

  // Filter and search tasks
  const filteredTasks = tasks.filter(task => {
    const matchesFilter = (
      (filter.status === 'all' || task.status === filter.status) &&
      (filter.priority === 'all' || task.priority === filter.priority) &&
      (filter.category === 'all' || task.category === filter.category)
    );
    
    const matchesSearch = !searchTerm || 
      task.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      task.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
      task.category.toLowerCase().includes(searchTerm.toLowerCase());
    
    return matchesFilter && matchesSearch;
  });

  // Toggle dark mode
  const toggleDarkMode = () => {
    const newDarkMode = !darkMode;
    setDarkMode(newDarkMode);
    document.body.classList.toggle('dark-mode');
    storageService.saveThemePreference(newDarkMode ? 'dark' : 'light');
  };

  // Calculate completion stats
  const completedCount = tasks.filter(task => task.status === 'completed').length;
  const totalCount = tasks.length;
  const completionPercentage = totalCount ? Math.round((completedCount / totalCount) * 100) : 0;

  // Get unique categories for filter dropdown
  const categories = ['all', ...tasks.map(task => task.category).filter((value, index, self) => self.indexOf(value) === index)];

  // Task entry animation
  const taskVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: (i: number) => ({ 
      opacity: 1, 
      y: 0,
      transition: {
        delay: i * 0.05,
        duration: 0.3
      }
    }),
    exit: { opacity: 0, x: -100, transition: { duration: 0.2 } }
  };

  // Категорії завдань
  const taskCategories = [
    { value: 'personal', label: 'Особисте' },
    { value: 'work', label: 'Робота' },
    { value: 'education', label: 'Освіта' },
    { value: 'health', label: 'Здоров\'я' },
    { value: 'financial', label: 'Фінанси' },
    { value: 'family', label: 'Сім\'я' }, 
    { value: 'creative', label: 'Творчість' },
    { value: 'spiritual', label: 'Духовний розвиток' }
  ];

  return (
    <div className={`app ${darkMode ? 'dark' : 'light'}`}>
      <header className="app-header">
        <h1>PlanPilot</h1>
        <button className="theme-toggle" onClick={toggleDarkMode}>
          {darkMode ? '☀️' : '🌙'}
        </button>
      </header>
      
      <main>
        <motion.section 
          className="task-stats"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.4 }}
        >
          <div className="stats-container">
            <div className="stat-item">
              <h4>Всього завдань</h4>
              <div className="stat-value">{totalCount}</div>
            </div>
            <div className="stat-item">
              <h4>Виконано</h4>
              <div className="stat-value">{completedCount}</div>
            </div>
            <PremiumFeature 
              featureKey="basic_analytics" 
              requiredPlan="basic"
              description="Отримайте доступ до розширеної аналітики для відстеження прогресу виконання завдань та аналізу вашої продуктивності."
            >
              <div className="stat-item">
                <h4>Прогрес</h4>
                <div className="stat-progress">
                  <div className="progress-bar">
                    <div 
                      className="progress-fill" 
                      style={{ width: `${completionPercentage}%` }}
                    ></div>
                  </div>
                  <span className="progress-text">{completionPercentage}%</span>
                </div>
              </div>
            </PremiumFeature>
          </div>
        </motion.section>
        
        <motion.section 
          className="task-filters"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.4, delay: 0.2 }}
        >
          <div className="filters-header">
            <h2>Фільтри та пошук</h2>
            <motion.button 
              className="add-task-btn"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => setShowAddForm(!showAddForm)}
            >
              {showAddForm ? 'Скасувати' : 'Нове завдання'}
            </motion.button>
          </div>

          <div className="search-container">
            <input
              type="text"
              placeholder="Пошук завдань..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="search-input"
            />
          </div>
          <div className="filters">
            <div className="filter-group">
              <label htmlFor="status-filter">Статус</label>
              <select
                id="status-filter"
                value={filter.status}
                onChange={(e) => setFilter(prev => ({ ...prev, status: e.target.value }))}
              >
                <option value="all">Всі</option>
                <option value="pending">Очікує</option>
                <option value="in-progress">В процесі</option>
                <option value="completed">Виконано</option>
              </select>
            </div>
            
            <div className="filter-group">
              <label htmlFor="priority-filter">Пріоритет</label>
              <select
                id="priority-filter"
                value={filter.priority}
                onChange={(e) => setFilter(prev => ({ ...prev, priority: e.target.value }))}
              >
                <option value="all">Всі</option>
                <option value="low">Низький</option>
                <option value="medium">Середній</option>
                <option value="high">Високий</option>
              </select>
            </div>
            
            <div className="filter-group">
              <label htmlFor="category-filter">Категорія</label>
              <select
                id="category-filter"
                value={filter.category}
                onChange={(e) => setFilter(prev => ({ ...prev, category: e.target.value }))}
              >
                {categories.map((category, index) => (
                  <option key={index} value={category}>
                    {category === 'all' ? 'Всі' : category}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </motion.section>
        
        <AnimatePresence>
          {showAddForm && (
            <motion.section 
              className="add-task-form"
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
              transition={{ duration: 0.3 }}
            >
              <h2>Нове завдання</h2>
              
              {/* Показуємо мотиваційну цитату, якщо вибрано категорію */}
              {showMotivationalQuote && (
                <PremiumFeature 
                  featureKey="basic_quotes" 
                  requiredPlan="basic"
                  description="Отримуйте мотиваційні цитати відповідно до категорії ваших цілей, щоб залишатися мотивованим і зосередженим."
                >
                  <MotivationalQuote category={selectedCategory} />
                </PremiumFeature>
              )}
              
              <form onSubmit={addTask}>
                <div className="form-group">
                  <label htmlFor="title">Назва</label>
                  <input
                    id="title"
                    type="text"
                    name="title"
                    value={newTask.title}
                    onChange={handleInputChange}
                    placeholder="Введіть назву завдання"
                    required
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="description">Опис</label>
                  <textarea
                    id="description"
                    name="description"
                    value={newTask.description}
                    onChange={handleInputChange}
                    placeholder="Опишіть деталі завдання"
                    rows={3}
                  />
                </div>
                <div className="form-row">
                  <div className="form-group">
                    <label htmlFor="category">Категорія</label>
                    <select
                      id="category"
                      name="category"
                      value={newTask.category}
                      onChange={handleInputChange}
                    >
                      {taskCategories.map(category => (
                        <option key={category.value} value={category.value}>
                          {category.label}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="form-group">
                    <label htmlFor="priority">Пріоритет</label>
                    <select
                      id="priority"
                      name="priority"
                      value={newTask.priority}
                      onChange={handleInputChange}
                    >
                      <option value="low">Низький</option>
                      <option value="medium">Середній</option>
                      <option value="high">Високий</option>
                    </select>
                  </div>
                </div>
                <div className="form-actions">
                  <button 
                    type="button" 
                    className="cancel-btn"
                    onClick={() => setShowAddForm(false)}
                  >
                    Скасувати
                  </button>
                  <button 
                    type="submit" 
                    className="submit-btn"
                  >
                    Додати завдання
                  </button>
                </div>
              </form>
            </motion.section>
          )}
        </AnimatePresence>
        
        <motion.section 
          className="task-list"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.4, delay: 0.4 }}
        >
          <h2>Ваші завдання</h2>
          
          {filteredTasks.length === 0 ? (
            <motion.div 
              className="no-tasks-message"
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4 }}
            >
              <p>У вас ще немає завдань в цій категорії. Додайте перше завдання!</p>
            </motion.div>
          ) : (
            <AnimatePresence>
              {filteredTasks.map((task, index) => (
                <TaskItem 
                  key={task.id}
                  task={task}
                  index={index}
                  onStatusChange={updateTaskStatus}
                  onDelete={deleteTask}
                />
              ))}
            </AnimatePresence>
          )}
        </motion.section>
      </main>
      
      <footer>
        <p>PlanPilot © {new Date().getFullYear()}</p>
      </footer>

      <motion.button 
        className="floating-action-btn"
        onClick={() => setShowAddForm(true)}
        initial={{ scale: 0, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        transition={{ delay: 0.5, type: 'spring' }}
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
      >
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <line x1="12" y1="5" x2="12" y2="19"></line>
          <line x1="5" y1="12" x2="19" y2="12"></line>
        </svg>
      </motion.button>
    </div>
  );
};

export default TaskApp; 