import React from 'react';
import { motion } from 'framer-motion';
import '../styles/MotivationalQuote.css';

// Типи для цитат
interface Quote {
  text: string;
  author: string;
}

interface QuotesDatabase {
  [category: string]: Quote[];
}

// База цитат для різних категорій
export const motivationalQuotes: QuotesDatabase = {
  personal: [
    { text: "Змінюй себе раніше, ніж тебе змусять це зробити обставини.", author: "Джек Велч" },
    { text: "Єдиний спосіб робити велику роботу — любити те, що ти робиш.", author: "Стів Джобс" },
    { text: "Твоє життя стає кращим не випадково, а лише завдяки змінам.", author: "Джим Рон" },
  ],
  work: [
    { text: "Секрет успіху – це знати, що ніхто насправді не знає, що він робить.", author: "Сунні Хаттон" },
    { text: "Успіх – це не ключ до щастя. Щастя – це ключ до успіху.", author: "Альберт Швейцер" },
    { text: "Якщо ви хочете досягти успіху, обмежте себе до однієї справи і вдосконалюйтесь у ній.", author: "Ден Рокфеллер" },
  ],
  education: [
    { text: "Освіта – це не підготовка до життя; освіта — це саме життя.", author: "Джон Дьюї" },
    { text: "Інвестиції в знання завжди дають найбільший прибуток.", author: "Бенджамін Франклін" },
    { text: "Все, що я знаю, це те, що я нічого не знаю.", author: "Сократ" },
  ],
  health: [
    { text: "Здоров'я — це стан повного фізичного, психічного і соціального благополуччя.", author: "ВООЗ" },
    { text: "Ваше тіло – це храм. Тримайте його чистим для душі, щоб мешкати.", author: "Б.К.С. Айенгар" },
    { text: "Найкращі ліки для здоров'я - це здоровий спосіб життя.", author: "Гіппократ" },
  ],
  financial: [
    { text: "Гроші не куплять щастя, але вони нададуть вам більш приємний вид пошуку його.", author: "Мілтон Берле" },
    { text: "Успіх — не в грошах, а в можливостях, які вони дають.", author: "Роберт Кійосакі" },
    { text: "Не економте на маленьких речах, якщо ви можете заробити на великих.", author: "Уоррен Баффет" },
  ],
  family: [
    { text: "Сім'я – це не важлива річ. Це все.", author: "Майкл Дж. Фокс" },
    { text: "Родина – один із шедеврів природи.", author: "Джордж Сантаяна" },
    { text: "Щасливі сім'ї схожі одна на одну, кожна нещаслива сім'я нещасна по-своєму.", author: "Лев Толстой" },
  ],
  creative: [
    { text: "Творчість вимагає хоробрості відпустити визначеність.", author: "Еріх Фром" },
    { text: "Найкращий спосіб мати хорошу ідею — мати багато ідей.", author: "Лайнус Полінг" },
    { text: "Ви не можете вичерпати творчість. Чим більше ви її використовуєте, тим більше її у вас є.", author: "Майя Анжелу" },
  ],
  spiritual: [
    { text: "Мета життя — саморозвиток. Реалізувати свою природу досконало — для цього ми тут.", author: "Оскар Вайльд" },
    { text: "Ми не людські істоти, що мають духовний досвід. Ми духовні істоти, що мають людський досвід.", author: "П'єр Тейяр де Шарден" },
    { text: "Шлях до успіху — це постійне прагнення до більшого.", author: "Будда" },
  ],
};

interface MotivationalQuoteProps {
  category: string;
}

const MotivationalQuote: React.FC<MotivationalQuoteProps> = ({ category }) => {
  // Отримати випадкову цитату для вибраної категорії
  const getRandomQuote = (category: string): Quote => {
    // Якщо категорії немає або немає цитат для неї, повертаємо узагальнену цитату
    if (!motivationalQuotes[category] || motivationalQuotes[category].length === 0) {
      return {
        text: "Шлях у тисячу миль починається з одного кроку.",
        author: "Лао-цзи"
      };
    }
    
    const quotes = motivationalQuotes[category];
    const randomIndex = Math.floor(Math.random() * quotes.length);
    return quotes[randomIndex];
  };

  const quote = getRandomQuote(category);

  return (
    <motion.div 
      className="motivational-quote"
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <div className="quote-icon">❝</div>
      <blockquote>{quote.text}</blockquote>
      <cite>— {quote.author}</cite>
    </motion.div>
  );
};

export default MotivationalQuote; 