// Type definitions for our data
export interface Task {
  id: string;
  title: string;
  description: string;
  status: 'pending' | 'in-progress' | 'completed';
  priority: 'low' | 'medium' | 'high';
  category: string;
  createdAt: string;
  completedAt?: string;
}

export interface TelegramInfo {
  connected: boolean;
  username?: string;
  name?: string;
  connectionCode?: string;
  codeExpires?: string;
}

export interface UserProfile {
  name: string;
  avatar: string;
  email: string;
  theme: 'light' | 'dark';
  createdAt: string;
  telegram?: TelegramInfo;
}

export interface Analytics {
  completedTasks: number;
  pendingTasks: number;
  inProgressTasks: number;
  byCategory: Record<string, number>;
  byPriority: Record<string, number>;
  weeklyCompletion: Record<string, number>;
}

export type SubscriptionPlan = 'free' | 'basic' | 'pro' | 'enterprise';

export interface Subscription {
  plan: SubscriptionPlan;
  startDate: string;
  endDate: string;
  active: boolean;
  paymentMethod?: string;
  autoRenew: boolean;
}

// Storage keys
const TASKS_STORAGE_KEY = 'planpilot_tasks';
const THEME_STORAGE_KEY = 'planpilot_theme';
const USER_PROFILE_KEY = 'planpilot_user_profile';
const SUBSCRIPTION_KEY = 'planpilot_subscription';

// Task storage functions
export const getTasks = (): Task[] => {
  try {
    const tasksJson = localStorage.getItem(TASKS_STORAGE_KEY);
    return tasksJson ? JSON.parse(tasksJson) : [];
  } catch (error) {
    console.error('Error reading tasks from localStorage:', error);
    return [];
  }
};

export const saveTasks = (tasks: Task[]): void => {
  try {
    localStorage.setItem(TASKS_STORAGE_KEY, JSON.stringify(tasks));
  } catch (error) {
    console.error('Error saving tasks to localStorage:', error);
  }
};

// Theme storage functions
export const getThemePreference = (): 'light' | 'dark' => {
  try {
    const savedTheme = localStorage.getItem(THEME_STORAGE_KEY);
    if (savedTheme === 'light' || savedTheme === 'dark') {
      return savedTheme;
    }
    
    // If no theme is saved, use system preference
    const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    return prefersDark ? 'dark' : 'light';
  } catch (error) {
    console.error('Error reading theme preference from localStorage:', error);
    return 'light';
  }
};

export const saveThemePreference = (theme: 'light' | 'dark'): void => {
  try {
    localStorage.setItem(THEME_STORAGE_KEY, theme);
  } catch (error) {
    console.error('Error saving theme preference to localStorage:', error);
  }
};

// Helper functions for tasks
export const createTask = (taskData: Omit<Task, 'id' | 'createdAt'>): Task => {
  return {
    ...taskData,
    id: Date.now().toString(),
    createdAt: new Date().toISOString()
  };
};

// Telegram helper functions
export const getTelegramBotUsername = (): string => {
  try {
    return process.env.REACT_APP_TELEGRAM_BOT_USERNAME || 'PlanPilotAssistantBot';
  } catch (error) {
    console.error('Error getting Telegram bot username:', error);
    return 'PlanPilotAssistantBot';
  }
};

// User profile functions
export const getUserProfile = (): UserProfile => {
  try {
    const profileJson = localStorage.getItem(USER_PROFILE_KEY);
    if (profileJson) {
      return JSON.parse(profileJson);
    }
    
    // Check if we have Telegram user info
    const telegramInfoJson = localStorage.getItem('telegram_user_info');
    let telegram: TelegramInfo | undefined;
    
    if (telegramInfoJson) {
      telegram = JSON.parse(telegramInfoJson);
    }
    
    // Default profile
    return {
      name: telegram?.name || 'Користувач',
      avatar: `${process.env.PUBLIC_URL}/avatars/avatar1.png`,
      email: '',
      theme: getThemePreference(),
      createdAt: new Date().toISOString(),
      telegram
    };
  } catch (error) {
    console.error('Error reading user profile from localStorage:', error);
    return {
      name: 'Користувач',
      avatar: `${process.env.PUBLIC_URL}/avatars/avatar1.png`,
      email: '',
      theme: 'light',
      createdAt: new Date().toISOString()
    };
  }
};

// Перевіряє валідність URL зображення
export const isValidImageUrl = async (url: string): Promise<boolean> => {
  // Для локальних шляхів у папці public завжди повертаємо true
  if (url.startsWith('/') || url.startsWith('./') || url.startsWith(process.env.PUBLIC_URL || '')) {
    return true;
  }
  
  // Handle Pinterest and similar URLs with CORS issues using a proxy
  if (url.includes('pinterest.com') || url.includes('pinimg.com')) {
    // For Pinterest URLs, we need to use a CORS proxy
    try {
      // Create a modified URL that uses a CORS proxy
      const proxyUrl = `https://api.allorigins.win/raw?url=${encodeURIComponent(url)}`;
      
      // Try to fetch the image using the proxy
      const response = await fetch(proxyUrl, { method: 'HEAD' });
      const contentType = response.headers.get('content-type');
      
      // Check if we got an image
      if (contentType && contentType.startsWith('image/')) {
        return true;
      }
      
      // If we can't determine the content type, still allow it as Pinterest URLs are often valid
      if (url.includes('pinimg.com')) {
        return true;
      }
      
      return false;
    } catch (error) {
      console.error('Error checking Pinterest URL:', error);
      // For Pinterest, assume true even if checking fails, as it's likely a CORS issue
      return url.includes('pinimg.com');
    }
  }
  
  // For other regular image URLs
  return new Promise((resolve) => {
    const img = new Image();
    img.onload = () => resolve(true);
    img.onerror = () => resolve(false);
    
    // Add a CrossOrigin attribute to help with CORS issues
    img.crossOrigin = 'anonymous';
    img.src = url;
    
    // Set a timeout in case the image takes too long to load
    setTimeout(() => resolve(false), 5000);
  });
};

// Збереження профілю користувача з додатковою перевіркою URL аватара
export const saveUserProfile = async (profile: UserProfile): Promise<void> => {
  try {
    // Перевіряємо, чи URL аватара починається з http:// або https://
    if (profile.avatar.startsWith('http://') || profile.avatar.startsWith('https://')) {
      // Для зовнішніх URL перевіряємо, чи можна завантажити зображення
      const isValid = await isValidImageUrl(profile.avatar);
      if (!isValid) {
        // Якщо URL невалідний, використовуємо аватар за замовчуванням
        profile.avatar = `${process.env.PUBLIC_URL}/avatars/avatar1.png`;
      }
    }
    
    localStorage.setItem(USER_PROFILE_KEY, JSON.stringify(profile));
  } catch (error) {
    console.error('Error saving user profile to localStorage:', error);
  }
};

// Analytics functions
export const getAnalytics = (): Analytics => {
  const tasks = getTasks();
  
  // Basic counts
  const completedTasks = tasks.filter(task => task.status === 'completed').length;
  const pendingTasks = tasks.filter(task => task.status === 'pending').length;
  const inProgressTasks = tasks.filter(task => task.status === 'in-progress').length;
  
  // By category
  const byCategory: Record<string, number> = {};
  tasks.forEach(task => {
    byCategory[task.category] = (byCategory[task.category] || 0) + 1;
  });
  
  // By priority
  const byPriority: Record<string, number> = {
    low: 0,
    medium: 0,
    high: 0
  };
  tasks.forEach(task => {
    byPriority[task.priority] += 1;
  });
  
  // Weekly completion
  const weeklyCompletion: Record<string, number> = {};
  const now = new Date();
  const oneWeekAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
  
  for (let i = 0; i < 7; i++) {
    const date = new Date(oneWeekAgo.getTime() + i * 24 * 60 * 60 * 1000);
    const dateString = date.toISOString().split('T')[0];
    weeklyCompletion[dateString] = 0;
  }
  
  tasks
    .filter(task => task.status === 'completed' && task.completedAt)
    .forEach(task => {
      if (task.completedAt) {
        const completedDate = task.completedAt.split('T')[0];
        if (weeklyCompletion[completedDate] !== undefined) {
          weeklyCompletion[completedDate] += 1;
        }
      }
    });
  
  return {
    completedTasks,
    pendingTasks,
    inProgressTasks,
    byCategory,
    byPriority,
    weeklyCompletion
  };
};

// Complete a task (with completion timestamp)
export const completeTask = (tasks: Task[], taskId: string): Task[] => {
  return tasks.map(task => 
    task.id === taskId 
      ? { ...task, status: 'completed', completedAt: new Date().toISOString() } 
      : task
  );
};

// Subscription functions
export const getSubscription = (): Subscription | null => {
  try {
    const subscriptionJson = localStorage.getItem(SUBSCRIPTION_KEY);
    return subscriptionJson ? JSON.parse(subscriptionJson) : null;
  } catch (error) {
    console.error('Error reading subscription from localStorage:', error);
    return null;
  }
};

export const saveSubscription = (subscription: Subscription): void => {
  try {
    localStorage.setItem(SUBSCRIPTION_KEY, JSON.stringify(subscription));
  } catch (error) {
    console.error('Error saving subscription to localStorage:', error);
  }
};

export const getCurrentPlan = (): SubscriptionPlan => {
  const subscription = getSubscription();
  
  if (!subscription || !subscription.active) {
    return 'free';
  }
  
  // Перевірка чи підписка не закінчилась
  const now = new Date();
  const endDate = new Date(subscription.endDate);
  
  if (now > endDate) {
    // Підписка закінчилась
    const updatedSubscription = { ...subscription, active: false };
    saveSubscription(updatedSubscription);
    return 'free';
  }
  
  return subscription.plan;
};

export const activateSubscription = (plan: SubscriptionPlan, durationMonths: number): Subscription => {
  const now = new Date();
  const startDate = now.toISOString();
  
  // Обчислюємо дату закінчення
  const endDate = new Date(now);
  endDate.setMonth(now.getMonth() + durationMonths);
  
  const subscription: Subscription = {
    plan,
    startDate,
    endDate: endDate.toISOString(),
    active: true,
    autoRenew: true
  };
  
  saveSubscription(subscription);
  return subscription;
};

export const cancelSubscription = (): void => {
  const subscription = getSubscription();
  
  if (subscription) {
    const updatedSubscription = { ...subscription, autoRenew: false };
    saveSubscription(updatedSubscription);
  }
};

export const canUseFeature = (feature: string): boolean => {
  const currentPlan = getCurrentPlan();
  
  // Базовий набір функцій, доступний всім
  const freeFeatures = [
    'basic_tasks',
    'basic_categories',
    'dark_mode'
  ];
  
  // Функції, доступні на базовому тарифі
  const basicFeatures = [
    ...freeFeatures,
    'unlimited_tasks',
    'unlimited_categories',
    'basic_quotes',
    'basic_analytics',
    'csv_export',
    'email_support'
  ];
  
  // Функції, доступні на професійному тарифі
  const proFeatures = [
    ...basicFeatures,
    'premium_quotes',
    'advanced_analytics',
    'advanced_export',
    'priority_support',
    'task_templates',
    'pomodoro_timer',
    'integrations'
  ];
  
  // Функції, доступні на корпоративному тарифі
  const enterpriseFeatures = [
    ...proFeatures,
    'team_boards',
    'team_analytics',
    'corporate_integration',
    'custom_reports',
    'personal_manager',
    'branding',
    'training',
    'api_access'
  ];
  
  // Перевірка доступності функції згідно з тарифом
  switch (currentPlan) {
    case 'enterprise':
      return enterpriseFeatures.includes(feature);
    case 'pro':
      return proFeatures.includes(feature);
    case 'basic':
      return basicFeatures.includes(feature);
    case 'free':
    default:
      return freeFeatures.includes(feature);
  }
};

// Export default object with all functions
export default {
  getTasks,
  saveTasks,
  getThemePreference,
  saveThemePreference,
  createTask,
  getUserProfile,
  saveUserProfile,
  isValidImageUrl,
  getAnalytics,
  completeTask,
  getSubscription,
  saveSubscription,
  getCurrentPlan,
  activateSubscription,
  cancelSubscription,
  canUseFeature,
  getTelegramBotUsername
}; 