import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

// Fix existing avatar paths in localStorage
const USER_PROFILE_KEY = 'planpilot_user_profile';
try {
  const profileJson = localStorage.getItem(USER_PROFILE_KEY);
  if (profileJson) {
    const profile = JSON.parse(profileJson);
    // Update any old avatar path format to the new format with PUBLIC_URL
    if (profile.avatar && (
      profile.avatar.startsWith('/avatars/') || 
      profile.avatar.startsWith('./avatars/')
    )) {
      // Extract just the filename part (e.g., avatar1.png)
      const avatarFile = profile.avatar.split('/').pop();
      profile.avatar = `${process.env.PUBLIC_URL}/avatars/${avatarFile}`;
      localStorage.setItem(USER_PROFILE_KEY, JSON.stringify(profile));
    }
  }
} catch (error) {
  console.error('Error updating avatar path in localStorage:', error);
}

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
