import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import storageService, { UserProfile, Subscription } from '../services/storageService';
import '../styles/ProfilePage.css';

const DEFAULT_AVATARS = [
  `${process.env.PUBLIC_URL}/avatars/avatar1.png`,
  `${process.env.PUBLIC_URL}/avatars/avatar2.png`,
  `${process.env.PUBLIC_URL}/avatars/avatar3.png`,
  `${process.env.PUBLIC_URL}/avatars/avatar4.png`,
  `${process.env.PUBLIC_URL}/avatars/avatar5.png`,
  `${process.env.PUBLIC_URL}/avatars/avatar6.png`,
];

// Add a helper function to handle avatar URLs
const getProxiedImageUrl = (url: string) => {
  // If it's a Pinterest or pinimg URL, use a CORS proxy
  if (url.includes('pinterest.com') || url.includes('pinimg.com')) {
    return `https://api.allorigins.win/raw?url=${encodeURIComponent(url)}`;
  }
  // Otherwise return the original URL
  return url;
};

const ProfilePage: React.FC = () => {
  const [profile, setProfile] = useState<UserProfile>(storageService.getUserProfile());
  const [subscription, setSubscription] = useState<Subscription | null>(storageService.getSubscription());
  const [avatarMenuOpen, setAvatarMenuOpen] = useState(false);
  const [customAvatarUrl, setCustomAvatarUrl] = useState('');
  const [editMode, setEditMode] = useState(false);
  const [formName, setFormName] = useState(profile.name);
  const [formEmail, setFormEmail] = useState(profile.email);
  const [isLoading, setIsLoading] = useState(false);
  const [darkMode, setDarkMode] = useState(false);

  useEffect(() => {
    // Save profile whenever it changes
    const saveProfile = async () => {
      await storageService.saveUserProfile(profile);
    };
    saveProfile();
  }, [profile]);

  useEffect(() => {
    const themePreference = storageService.getThemePreference();
    setDarkMode(themePreference === 'dark');
    
    // Оновлення підписки (на випадок, якщо вона закінчилась)
    const currentPlan = storageService.getCurrentPlan();
    if (subscription && subscription.plan !== currentPlan) {
      setSubscription(storageService.getSubscription());
    }
  }, []);

  const handleAvatarSelect = (avatar: string) => {
    setProfile({...profile, avatar});
    setAvatarMenuOpen(false);
  };

  const handleCustomAvatarSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (customAvatarUrl.trim()) {
      setIsLoading(true);
      
      try {
        // Ensure URL has protocol
        let urlToCheck = customAvatarUrl.trim();
        if (!urlToCheck.startsWith('http://') && !urlToCheck.startsWith('https://')) {
          urlToCheck = 'https://' + urlToCheck;
        }
        
        // Special case for Pinterest
        const isPinterestUrl = urlToCheck.includes('pinterest') || urlToCheck.includes('pinimg.com');
        
        if (isPinterestUrl) {
          console.log('Processing Pinterest URL:', urlToCheck);
        }
        
        // Перевіряємо валідність URL зображення
        const isValid = await storageService.isValidImageUrl(urlToCheck);
        
        if (isValid) {
          setProfile({...profile, avatar: urlToCheck});
          setAvatarMenuOpen(false);
          setCustomAvatarUrl('');
          
          // Provide feedback for Pinterest URLs
          if (isPinterestUrl) {
            console.log('Pinterest image accepted successfully');
          }
        } else {
          if (isPinterestUrl) {
            alert('Не вдалося використати це зображення з Pinterest. Спробуйте скопіювати пряме посилання на зображення (відкрийте зображення у новій вкладці та скопіюйте URL).');
          } else {
            alert('Неможливо завантажити зображення за цим URL. Будь ласка, спробуйте інший URL, що веде безпосередньо до зображення (.jpg, .png, .gif тощо).');
          }
        }
      } catch (error) {
        console.error('Error checking image URL:', error);
        alert('Сталася помилка при перевірці зображення.');
      } finally {
        setIsLoading(false);
      }
    }
  };

  const handleProfileUpdate = (e: React.FormEvent) => {
    e.preventDefault();
    setProfile({
      ...profile,
      name: formName,
      email: formEmail,
    });
    setEditMode(false);
  };

  // Функція скасування підписки
  const handleCancelSubscription = () => {
    if (window.confirm('Ви впевнені, що хочете скасувати автоматичне продовження підписки?')) {
      storageService.cancelSubscription();
      setSubscription(storageService.getSubscription());
    }
  };
  
  // Залишилось днів підписки
  const getRemainingDays = (): number => {
    if (!subscription || !subscription.active) return 0;
    
    const now = new Date();
    const endDate = new Date(subscription.endDate);
    const diffTime = endDate.getTime() - now.getTime();
    return Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  };

  // Форматування дати
  const formatDate = (dateString: string): string => {
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('uk-UA', { 
        day: 'numeric', 
        month: 'long', 
        year: 'numeric' 
      });
    } catch (e) {
      return dateString;
    }
  };
  
  // Отримання назви плану для відображення
  const getPlanDisplayName = (): string => {
    if (!subscription || !subscription.active) {
      return 'Безкоштовний';
    }
    
    switch (subscription.plan) {
      case 'basic':
        return 'Базовий';
      case 'pro':
        return 'Професійний';
      case 'enterprise':
        return 'Корпоративний';
      default:
        return 'Безкоштовний';
    }
  };

  return (
    <motion.div 
      className="profile-page"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <div className="profile-header">
        <h1>Мій профіль</h1>
      </div>

      <div className="profile-card">
        <div className="avatar-section">
          <div 
            className="avatar-container"
            onClick={() => setAvatarMenuOpen(!avatarMenuOpen)}
          >
            <img 
              src={getProxiedImageUrl(profile.avatar)} 
              alt="Avatar" 
              className="profile-avatar"
              onError={(e) => {
                const target = e.target as HTMLImageElement;
                target.src = `${process.env.PUBLIC_URL}/avatars/avatar1.png`;
              }}
            />
            <div className="avatar-edit-badge">
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
              </svg>
            </div>
          </div>

          {avatarMenuOpen && (
            <motion.div 
              className="avatar-menu"
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              transition={{ type: "spring", stiffness: 300, damping: 20 }}
            >
              <div className="avatar-options">
                {DEFAULT_AVATARS.map((avatar, index) => (
                  <img 
                    key={index}
                    src={avatar}
                    alt={`Avatar option ${index + 1}`}
                    onClick={() => handleAvatarSelect(avatar)}
                    className="avatar-option"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement;
                      target.src = `${process.env.PUBLIC_URL}/avatars/avatar1.png`;
                    }}
                  />
                ))}
              </div>
              
              <div className="custom-avatar-section">
                <h4>Додати зображення за URL</h4>
                <p className="url-hint">Ви можете додати фото з Pinterest, Instagram, Unsplash та інших сайтів.</p>
                <form onSubmit={handleCustomAvatarSubmit}>
                  <input
                    type="text"
                    value={customAvatarUrl}
                    onChange={(e) => setCustomAvatarUrl(e.target.value)}
                    placeholder="Вставте URL зображення (https://...)"
                    disabled={isLoading}
                  />
                  <div className="url-examples">
                    <small>Підтримуються прямі посилання на зображення та посилання з Pinterest.</small>
                  </div>
                  <div className="url-help-tooltip">
                    <span className="tooltip-icon">?</span>
                    <div className="tooltip-content">
                      <p><strong>Як отримати URL зображення з Pinterest:</strong></p>
                      <ol>
                        <li>Відкрийте зображення на Pinterest</li>
                        <li>Натисніть правою кнопкою миші на зображення</li>
                        <li>Виберіть "Копіювати URL зображення" або "Копіювати адресу зображення"</li>
                        <li>Вставте цю адресу в поле вище</li>
                      </ol>
                      <p>URL повинен закінчуватися на .jpg, .png, або .webp</p>
                    </div>
                  </div>
                  <button 
                    type="submit" 
                    disabled={!customAvatarUrl.trim() || isLoading}
                    className="url-submit-btn"
                  >
                    {isLoading ? 'Перевірка...' : 'Встановити'}
                  </button>
                </form>
                
                {isLoading && (
                  <div className="loading-indicator">
                    <div className="spinner"></div>
                    <span>Перевірка зображення...</span>
                  </div>
                )}
              </div>
              
              <button 
                className="close-avatar-menu"
                onClick={() => setAvatarMenuOpen(false)}
                disabled={isLoading}
              >
                Закрити
              </button>
            </motion.div>
          )}
        </div>

        {!editMode ? (
          <div className="profile-info">
            <h2>{profile.name}</h2>
            {profile.email && <p className="profile-email">{profile.email}</p>}
            <p className="profile-member-since">Учасник з {formatDate(profile.createdAt)}</p>
            
            <button 
              className="edit-profile-button"
              onClick={() => setEditMode(true)}
            >
              Редагувати профіль
            </button>
          </div>
        ) : (
          <motion.div 
            className="profile-edit-form"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
          >
            <form onSubmit={handleProfileUpdate}>
              <div className="form-group">
                <label htmlFor="name">Ім'я</label>
                <input
                  type="text"
                  id="name"
                  value={formName}
                  onChange={(e) => setFormName(e.target.value)}
                  required
                />
              </div>
              
              <div className="form-group">
                <label htmlFor="email">Електронна пошта</label>
                <input
                  type="email"
                  id="email"
                  value={formEmail}
                  onChange={(e) => setFormEmail(e.target.value)}
                />
              </div>
              
              <div className="form-actions">
                <button type="submit" className="save-button">Зберегти</button>
                <button 
                  type="button" 
                  className="cancel-button"
                  onClick={() => {
                    setEditMode(false);
                    setFormName(profile.name);
                    setFormEmail(profile.email);
                  }}
                >
                  Скасувати
                </button>
              </div>
            </form>
          </motion.div>
        )}
      </div>

      <div className="profile-preferences">
        <h3>Налаштування</h3>
        <div className="preference-item">
          <span>Темна тема</span>
          <div 
            className={`theme-toggle ${darkMode ? 'active' : ''}`}
            onClick={() => {
              const newTheme = darkMode ? 'light' : 'dark';
              setProfile({...profile, theme: newTheme});
              document.body.classList.toggle('dark-mode', newTheme === 'dark');
              storageService.saveThemePreference(newTheme);
            }}
          >
            <div className="toggle-handle"></div>
          </div>
        </div>
      </div>

      {/* Telegram Connection Section */}
      <div className="profile-section">
        <h3>Підключення Telegram</h3>
        <div className="telegram-connection">
          {profile.telegram?.connected ? (
            <div className="telegram-connected">
              <div className="telegram-status">
                <span className="telegram-status-icon">✅</span>
                <span>Підключено до Telegram</span>
              </div>
              <p>Ви можете отримувати сповіщення та керувати завданнями через бот.</p>
              <button 
                className="disconnect-button"
                onClick={() => {
                  if (window.confirm('Ви впевнені, що хочете відключити Telegram?')) {
                    setProfile({...profile, telegram: { connected: false }});
                  }
                }}
              >
                Відключити
              </button>
            </div>
          ) : (
            <div className="telegram-disconnected">
              <p>Підключіть ваш Telegram акаунт, щоб отримувати сповіщення про завдання та керувати ними через бот.</p>
              <div className="telegram-connect-options">
                <div className="connection-code-section">
                  <button 
                    className="generate-code-button"
                    onClick={() => {
                      // Генерація випадкового 6-значного коду
                      const randomCode = Math.floor(100000 + Math.random() * 900000);
                      // Зберігаємо код у localStorage для демонстраційних цілей
                      localStorage.setItem('telegram_connection_code', randomCode.toString());
                      // Оновлюємо стан компонента, щоб показати код
                      setProfile({
                        ...profile, 
                        telegram: { 
                          connected: false,
                          connectionCode: randomCode.toString(),
                          codeExpires: new Date(Date.now() + 10 * 60 * 1000).toISOString() // 10 хвилин
                        }
                      });
                    }}
                  >
                    Згенерувати код підключення
                  </button>
                  
                  {profile.telegram?.connectionCode && (
                    <div className="connection-code-display">
                      <h4>Ваш код підключення:</h4>
                      <div className="code">{profile.telegram.connectionCode}</div>
                      <p className="expires-in">
                        Код дійсний протягом 10 хвилин. Використайте його в Telegram боті з командою:
                      </p>
                      <pre className="connect-command">/connect {profile.telegram.connectionCode}</pre>
                    </div>
                  )}
                </div>
                
                <a 
                  href={`https://t.me/${storageService.getTelegramBotUsername()}`} 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="telegram-connect-button"
                >
                  Відкрити Telegram бота
                </a>
                <div className="telegram-connect-code">
                  <p>Після відкриття бота, введіть команду з кодом підключення:</p>
                  <pre>/connect КОД</pre>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      <div className="app-info">
        <h3>Про додаток</h3>
        <p>PlanPilot v1.0.0</p>
        <p>Ваш особистий помічник у плануванні завдань та досягненні цілей</p>
      </div>

      <motion.div 
        className="subscription-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.1 }}
      >
        <div className="subscription-header">
          <h2>Ваша підписка</h2>
          <span className={`plan-badge ${subscription?.active ? subscription.plan : 'free'}`}>
            {getPlanDisplayName()}
          </span>
        </div>
        
        {subscription && subscription.active ? (
          <>
            <div className="subscription-details">
              <div className="detail-row">
                <span className="detail-label">Початок:</span>
                <span className="detail-value">{formatDate(subscription.startDate)}</span>
              </div>
              <div className="detail-row">
                <span className="detail-label">Закінчення:</span>
                <span className="detail-value">{formatDate(subscription.endDate)}</span>
              </div>
              <div className="detail-row">
                <span className="detail-label">Автопродовження:</span>
                <span className="detail-value">
                  {subscription.autoRenew ? 'Так' : 'Ні'}
                </span>
              </div>
              <div className="detail-row">
                <span className="detail-label">Залишилось:</span>
                <span className="detail-value">{getRemainingDays()} днів</span>
              </div>
            </div>
            
            <div className="subscription-actions">
              {subscription.autoRenew && (
                <button 
                  className="cancel-subscription-btn"
                  onClick={handleCancelSubscription}
                >
                  Скасувати автопродовження
                </button>
              )}
              <Link to="/subscription" className="change-plan-btn">
                Змінити план
              </Link>
            </div>
          </>
        ) : (
          <div className="subscription-upsell">
            <p>
              Ви використовуєте безкоштовний план з обмеженими можливостями.
              Спробуйте преміум функції з нашими платними планами!
            </p>
            <Link to="/subscription" className="subscribe-btn">
              Вибрати план
            </Link>
          </div>
        )}
      </motion.div>

      <div className="profile-footer">
        <Link to="/app" className="back-to-app">
          Повернутися до додатку
        </Link>
      </div>
    </motion.div>
  );
};

export default ProfilePage; 