import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Task } from '../services/storageService';
import { format } from 'date-fns';
import { uk } from 'date-fns/locale';
import MotivationalQuote from './MotivationalQuote';

interface TaskItemProps {
  task: Task;
  index: number;
  onStatusChange: (id: string, status: Task['status']) => void;
  onDelete: (id: string) => void;
}

const TaskItem: React.FC<TaskItemProps> = ({ task, index, onStatusChange, onDelete }) => {
  const [expanded, setExpanded] = useState(false);
  const [showQuote, setShowQuote] = useState(false);

  // Форматування дати створення
  const formatDate = (dateString: string) => {
    try {
      const date = new Date(dateString);
      return format(date, 'd MMMM yyyy', { locale: uk });
    } catch (e) {
      return dateString;
    }
  };

  // Обробник натискання на категорію для показу мотиваційної цитати
  const handleCategoryClick = () => {
    setShowQuote(!showQuote);
    // Якщо цитата вже показується, закриваємо її, інакше показуємо на 10 секунд
    if (!showQuote) {
      setTimeout(() => {
        setShowQuote(false);
      }, 10000);
    }
  };

  // Класи для пріоритету
  const priorityClass = `priority-${task.priority}`;
  
  // Класи для категорії
  const categoryClass = `category-${task.category}`;

  return (
    <motion.div 
      className={`task-item ${task.status === 'completed' ? 'completed' : ''}`}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, x: -100 }}
      transition={{ delay: index * 0.05 }}
      layout
    >
      <div className="task-header" onClick={() => setExpanded(!expanded)}>
        <div className="task-status-checkbox">
          <input
            type="checkbox"
            checked={task.status === 'completed'}
            onChange={() => onStatusChange(task.id, task.status === 'completed' ? 'pending' : 'completed')}
            onClick={(e) => e.stopPropagation()}
          />
        </div>
        
        <div className="task-main-info">
          <h3 className="task-title">{task.title}</h3>
          <div className="task-meta">
            <span 
              className={`task-category ${categoryClass}`}
              onClick={(e) => {
                e.stopPropagation();
                handleCategoryClick();
              }}
            >
              {task.category}
            </span>
            <span className={`task-priority ${priorityClass}`}>
              {task.priority === 'high' ? '🔴' : task.priority === 'medium' ? '🟠' : '🟢'}
              {' '}
              {task.priority}
            </span>
          </div>
        </div>
        
        <div className="task-actions">
          <button 
            className="delete-task-btn"
            onClick={(e) => {
              e.stopPropagation();
              onDelete(task.id);
            }}
          >
            ❌
          </button>
          <span className="expand-icon">
            {expanded ? '▲' : '▼'}
          </span>
        </div>
      </div>
      
      {expanded && (
        <motion.div 
          className="task-details"
          initial={{ opacity: 0, height: 0 }}
          animate={{ opacity: 1, height: 'auto' }}
          exit={{ opacity: 0, height: 0 }}
        >
          {task.description && (
            <div className="task-description">
              <h4>Опис:</h4>
              <p>{task.description}</p>
            </div>
          )}
          
          <div className="task-dates">
            <p>Створено: {formatDate(task.createdAt)}</p>
            {task.completedAt && (
              <p>Виконано: {formatDate(task.completedAt)}</p>
            )}
          </div>
          
          <div className="task-status-actions">
            <button 
              className={`status-btn pending ${task.status === 'pending' ? 'active' : ''}`}
              onClick={() => onStatusChange(task.id, 'pending')}
            >
              Очікує
            </button>
            <button 
              className={`status-btn in-progress ${task.status === 'in-progress' ? 'active' : ''}`}
              onClick={() => onStatusChange(task.id, 'in-progress')}
            >
              В процесі
            </button>
            <button 
              className={`status-btn completed ${task.status === 'completed' ? 'active' : ''}`}
              onClick={() => onStatusChange(task.id, 'completed')}
            >
              Завершено
            </button>
          </div>
        </motion.div>
      )}
      
      {showQuote && (
        <motion.div 
          className="task-quote-container"
          initial={{ opacity: 0, height: 0 }}
          animate={{ opacity: 1, height: 'auto' }}
          exit={{ opacity: 0, height: 0 }}
        >
          <MotivationalQuote category={task.category} />
        </motion.div>
      )}
    </motion.div>
  );
};

export default TaskItem; 