import React from 'react';
import * as FiIcons from 'react-icons/fi';

// Icon wrapper components using function call instead of JSX
export const CheckIcon: React.FC = () => {
  const icon = FiIcons.FiCheck({});
  return <span>{icon}</span>;
};

export const XIcon: React.FC = () => {
  const icon = FiIcons.FiX({});
  return <span>{icon}</span>;
};

export const ChevronLeftIcon: React.FC<{style?: React.CSSProperties}> = ({style}) => {
  const icon = FiIcons.FiChevronLeft({});
  return <span style={style}>{icon}</span>;
};

export const AwardIcon: React.FC<{style?: React.CSSProperties}> = ({style}) => {
  const icon = FiIcons.FiAward({});
  return <span style={style}>{icon}</span>;
};

export const InfoIcon: React.FC = () => {
  const icon = FiIcons.FiInfo({});
  return <span>{icon}</span>;
}; 